.header {
  justify-content: space-between;
  padding: 30px;
  background: #FFFFFF;
  border: 1px solid #F8F8F8; }
  .header__burger-menu {
    text-align: center; }
    .header__burger-menu img {
      vertical-align: sub; }
    .header__burger-menu .fa-bars {
      padding-right: 15px; }
  .header__search-bar {
    display: flex;
    justify-content: flex-end; }
    .header__search-bar .change-language {
      display: flex;
      font-weight: 500;
      font-size: 18px;
      text-transform: uppercase; }
      .header__search-bar .change-language_ru {
        color: red;
        padding: 0 10px; }
      .header__search-bar .change-language_ua {
        color: #000;
        padding: 0 10px; }
    .header__search-bar__search {
      font-size: 20px; }

.category-row {
  display: flex;
  justify-content: center; }
  .category-row__item {
    text-align: center; }
    .category-row__item span {
      text-transform: uppercase;
      color: #000000;
      font-size: 18px;
      font-weight: 700; }

.modal-body .form-control {
  padding-right: 3rem; }

.modal-body i {
  cursor: pointer;
  position: absolute;
  right: 27px;
  top: 25%; }
