.scroll {
  position: relative;
  margin-top: 45px; }
  .scroll img {
    width: 100%; }
  .scroll .scroll-title {
    padding: 3%;
    font-size: 40px;
    position: absolute;
    color: #ffffff;
    width: 37%;
    text-transform: uppercase;
    top: 0; }
    .scroll .scroll-title__link {
      font-size: 16px;
      opacity: 0.5;
      margin-top: 15%; }
      .scroll .scroll-title__link a {
        color: #ffffff;
        text-decoration: none; }

.scrollbar {
  margin: 20px;
  right: 0;
  top: 0;
  position: absolute;
  float: left;
  height: -webkit-fill-available;
  width: 480px;
  background: #f5f5f500;
  overflow-y: scroll; }

.force-overflow {
  min-height: 450px; }

#wrapper {
  text-align: center;
  width: 500px;
  margin: auto; }

#style-6::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f500; }

#style-6::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f500; }

#style-6::-webkit-scrollbar-thumb {
  background-color: #ED3237; }

.scroll-item {
  position: relative; }
  .scroll-item .news-content {
    border-bottom: none;
    text-align: left;
    padding-top: 5px; }
    .scroll-item .news-content:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      height: 1px;
      width: 10%;
      border-bottom: 1px solid #ffffff;
      opacity: 0.5; }
    .scroll-item .news-content .post-text {
      font-size: 20px;
      color: #FFFFFF; }
    .scroll-item .news-content .post-time {
      color: #ffffff; }
